@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Teko:wght@700&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* 
=====
sass styling
=====
*/
$primary-color: black;
$secondary-color: white;
$trim-color:#DAA520;
$border-radius: 5px;
$margin: 20px;
$letter-spacing: 2px;
$teko: 'Teko', sans-serif;
$roboto: 'Roboto', sans-serif;
/* 
=====
sass styling
=====
*/

Nav {
  font-family: $teko;
}

//Footer
#footercopy {
  font-size: 1.15rem;
  font-family: $teko;
}
#socialicons {
  color: $secondary-color;
  text-decoration: none;
  margin-bottom: 0.5rem;
  font-size: 25px;
  margin: 20px;
  cursor: pointer;
}
#socialicons:hover {
  color: $trim-color;
  transition: 0.3s ease-out;
}
#footer-built {
  font-size: 1.15rem;
  margin: 0;
  letter-spacing: 0px;
  color: $secondary-color;
  text-decoration: none;
  font-family: $teko;
}
#footer-built:hover{
  color: $trim-color;
  transition: 0.3s ease-out;
}

//Register
.registercontainer {
  margin: 80px;
  padding: 10px;
  font-size: 1rem;
  text-align: center;
  word-spacing: 1px;
  font-family: $roboto;
  line-height: 26px;
}
.registercontainer h1 {
  font-family: $teko;
}
#registercontent {
  width: 60%;
  margin: 20px auto;
  text-align: left;
}
.scrolldown {
  width: 100%;
  margin: 70px auto;
}

@media screen and (max-width: 1024px) {
  #registercontent {
    width: 80%;
    margin: 20px auto;
  }
}

