//Form
#form-info {
  margin: 20px auto;
  font-weight: 600;
  font-size: 0.9rem;
}
.input-bx {
  width: 80%;
  padding: 10px;
  border-radius: $border-radius;
  outline: 3px solid black;
  // border: 3px solid black;
  margin: 15px auto;
  font-size: 0.9rem;
  font-weight: 600;
}
.area-bx {
  width: 80%;
  height: 80px;
  padding: 10px;
  border-radius: $border-radius;
  outline: 3px solid black;
  // border: 3px solid black;
  margin: 15px auto;
  font-size: 0.9rem;
  font-weight: 600;
}

