  .input-bx {
    width: 100%;
  }
  .area-bx {
    width: 100%;
    margin: 0;
  }
}

#blk-btn {
  color: $secondary-color;
  background-color: $primary-color;
  border-radius: $border-radius;
  font-family: $teko;
  border: none;
  padding: 10px;
  margin: 60px auto;
  width: 150px;
  font-size: 1.1rem;
  border-style: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
#blk-btn:hover {
  color: $trim-color;
  transition: 0.3s ease-in-out;
}
#gld-btn {
  color: $primary-color;
  background-color: $trim-color;
  border-radius: $border-radius;
  font-family: $teko;
  border: none;
  padding: 10px;
  margin: 10px auto;
  width: 180px;
  font-size: 1.1rem;
  border-style: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
#gld-btn:hover {
