//news
.news-first-section {
  margin: 90px auto;
  overflow-x: hidden;
  width: 70%;
}
.news-second-section {
  margin: 90px auto;
  overflow-x: hidden;
  width: 50%;
}
.news-hero {
  text-align: center;
}
.news-hero img {
  width: 1000px;
  height: 600px;
  border-radius: $border-radius;
}
.news-date {
  width: 70%;
  margin: 15px auto;
  margin-top: 20px;
  color: $trim-color;
  letter-spacing: $letter-spacing;
}
.news-title {
  width: 70%;
  margin: 15px auto;
  font-size: 2rem;
  text-transform: uppercase;
  font-family: $teko;
  text-decoration: 8px solid $trim-color underline;
}
.news-content p {
  width: 70%;
  margin: 15px auto;
}




@media screen and (max-width: 1024px) {
  .news-hero img {
    width: 100%;
    height: auto;
    border-radius: $border-radius;
  }
  .news-date, .news-title, .news-content p {
    width: 100%;
  } 
}

@media screen and (max-width: 768px) {
  .news-title {
   font-size: 1.9rem;
  } 
}

@media screen and (max-width: 720px) {
  .news-title {
    font-size: 1.8rem;
   } 
}

@media screen and (max-width: 600px) {
  .news-first-section {
    width: 80%;
}
  .news-title {
    font-size: 1.5rem;
   } 
}
//news